function createLayer(src) {
  const source = new GeoTIFF({
    sources: src.sources,
    projection: src.projection,
  });

  const layer = new TileLayer(
    {
      source: source,
      projection: src.projection,
      style: {
        variables: getVariables(),
        color: ['interpolate', ['linear'], maxh, ...colors()],
      },
    }
  )
  layer.setOpacity(rasterOpacity);
  return layer;
}
