function updateTimeFrame() {
  const visualization = timeframes[visualizationSelector.selectedIndex];
  const layer = createLayer(visualization);
  const newLayer = layer !== previousLayer;
  previousLayer = layer;
  map.setLayers([osm, layer]);

  // map.setView(layer.getSource().getView());
  return layer;
}

visualizationSelector.addEventListener('change', updateTimeFrame);
