const output = document.getElementById('output');
function displayPixelValue(event) {
  const data = curr_layer.getData(event.pixel);
  if (!data) {
    return;
  }
  // const red = data[0];
  // const nir = data[1];
  // const maxh = (nir - red) / (nir + red);
  const hgt = 20 * data[0] / 255;
  output.textContent = hgt.toFixed(2);
}
