// Popup showing the position the user click
const popup = new Overlay({
  element: document.getElementById('popup'),
});

const element = popup.getElement();
map.on('click', function (evt) {
  const coordinate = evt.coordinate;
  const value = displayPixelValue(evt);
  const hdms = toStringHDMS(toLonLat(coordinate));
  popup.setPosition(coordinate);
  let popover = bootstrap.Popover.getInstance(element);
  if (popover) {
    popover.dispose();
  }
  popover = new bootstrap.Popover(element, {
    animation: false,
    container: element,
    // content: '<p>The location you clicked was:</p><code>' + hdms + '</code><br/>' + toStringXY(coordinate),
    content: '<p>The location you clicked was:</p><code>' + hdms + '</code><br/><code>' + value + '</code>',
    html: true,
    placement: 'top',
    title: 'Position popup',
  });
  popover.show();
});

map.addOverlay(popup);
